@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    font-family: 'poppins';
    }
    .options{
        font-weight: 400;
        font-size: 0.9rem;
        padding-top: 5rem !important;
    }
    .active{
    font-weight: 600 !important;
    border-bottom: 2px solid black !important;
    }
    .button{
        border-radius: 0 !important;
    }
    .navswitch{
        margin-bottom: 4rem;
    }
/* Styles go here */

.Navbar-container{
width:100%;
height:10%;
font-family: 'poppins';
color: rgba(255, 255, 255, 0.8);
font-weight: 400;
font-size: 1rem;
justify-content: space-between;
display: flex;

}
.Navbar-container::before{
    content: '' ;
    background-image: url("/static/media/Group 2navbg.e3070ead.svg");
    position: absolute;
    width: 100%;
    height: 10%;
    background-repeat: no-repeat;
    top: 0;
    z-index: -1;
    left: 0;
}

.nav-heading{
    align-self: center;
    margin-left: 2rem;
}
.account{
    color:bisque;
    font-size: 1rem;
    align-self: center;
}
a{
    text-decoration: none;
    color:aliceblue;
    
}
.Navbar{
    width:100%;
    margin-top:10px;
    display:flex;
    justify-content:space-around;
    color:antiquewhite;
}

.product-history-container{
    position: fixed;
    top:20vh;
    left:10vw;
    height: 70vh;
    width: 80vw;
    background-color: rgb(81, 162, 233);
}

.product-history-close{
    position: absolute;
    right: 10px;
    top:20px;
    cursor: pointer;
}
